@charset "UTF-8";
@keyframes arrowStr2 {
  0% {
    top: 3px;
  }
  50% {
    top: -2px;
  }
  100% {
    top: 3px;
  }
}
@keyframes fromB2 {
  0% {
    opacity: 0;
    bottom: -30px;
  }
  100% {
    opacity: 1;
    bottom: 0;
  }
}
@keyframes toB2 {
  100% {
    opacity: 0;
    bottom: -30px;
  }
  0% {
    opacity: 1;
    bottom: 0;
  }
}
@keyframes fromL {
  0% {
    opacity: 0;
    right: 15px;
  }
  100% {
    opacity: 1;
    right: 0;
  }
}
@keyframes fromR {
  0% {
    opacity: 0;
    left: 15px;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
@keyframes fromT {
  0% {
    opacity: 0;
    top: -15px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes fromB {
  0% {
    opacity: 0;
    top: 15px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes toL {
  100% {
    opacity: 0;
    right: 15px;
  }
  0% {
    opacity: 1;
    right: 0;
  }
}
@keyframes toR {
  100% {
    opacity: 0;
    left: 15px;
  }
  0% {
    opacity: 1;
    left: 0;
  }
}
@keyframes toT {
  100% {
    opacity: 0;
    top: -15px;
  }
  0% {
    opacity: 1;
    top: 0;
  }
}
@keyframes toB {
  100% {
    opacity: 0;
    top: 15px;
  }
  0% {
    opacity: 1;
    top: 0;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes zoomIn {
  0%, 10%, 20%, 30%, 100% {
    transform: scale(1);
  }
  5%, 15%, 25% {
    transform: scale(1.1);
  }
}
@keyframes footPrint {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes rotationY {
  0% {
    opacity: 0;
    transform: rotateY(0deg);
  }
  33% {
    opacity: 1;
    transform: rotateY(360deg);
  }
  66% {
    opacity: 1;
    transform: rotateY(0deg);
  }
  100% {
    opacity: 1;
    transform: rotateY(360deg);
  }
}
@keyframes shake {
  0% {
    transform: rotate3D(10, 10, 10, 0deg);
  }
  25% {
    transform: rotate3D(10, 10, 10, 10deg);
  }
  50% {
    transform: rotate3D(10, 10, 10, 0deg);
  }
  75% {
    transform: rotate3D(50, 10, 30, 35deg);
  }
  100% {
    transform: rotate3D(10, 10, 10, 0deg);
  }
}
@keyframes nabi {
  0% {
    top: calc(50% - 230px);
    transform: rotateX(0deg);
  }
  50% {
    top: calc(50% - 224px);
    transform: rotateX(30deg);
  }
  100% {
    top: calc(50% - 230px);
    transform: rotateX(0deg);
  }
}
@-webkit-keyframes bugfix {
  from {
    padding: 0;
  }
  to {
    padding: 0;
  }
}
