  top: calc(50% - 489px);
  left: calc(50% - 627px);
  animation: shake 3s ease-in-out infinite;
}
#contact .el02 {
  top: calc(50% - 12px);
  left: calc(50% + 466px);
  animation: shake 3s ease-in-out 1.5s infinite;
}
#contact .el03 {
  top: calc(50% - 230px);
  left: calc(50% + 50px);
  animation: nabi 4s ease-in-out infinite;
  transform-origin: 38px 107px;
}
